@import '../../index.scss';
@import '../../shared/styles/index';

.income_Wrapper {
  @include media-query(tablet-only) {
    background-color: white;
    box-shadow: 0px 10px 60px rgba(170, 178, 197, 0.2);
    border-radius: 0px 30px 30px 30px;
    height: 616px;
    padding: 20px 40px 42px 40px;
  }
  @include media-query(desktop) {
    height: 579px;
    background-color: white;
    box-shadow: 0px 10px 60px rgba(170, 178, 197, 0.2);
    border-radius: 0px 30px 30px 30px;
    padding: 32px 32px 0 32px;
    position: relative;
  }
}

.firstSection_Wrapper {
  @include media-query(tablet-only) {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 4px 0 32px 0;
  }
  @include media-query(desktop) {
    display: flex;
    align-items: center;
    margin-bottom: 60px;
  }
}

.btn {
  @include media-query(desktop) {
    width: 136px;
    height: 44px;

    font-weight: 700;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: $color-secondary;
    background: #ffffff;
    border: 2px solid #f6f7fc;
    border-radius: 16px;
    margin-left: 16px;
    &:hover,
    &:focus {
      color: #ffffff;
      background: $color-accent;
    }
  }
  @include media-query(tablet-only) {
    width: 136px;
    height: 44px;

    font-weight: 700;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: $color-secondary;
    background: #ffffff;
    border: 2px solid #f6f7fc;
    border-radius: 16px;

    &:hover,
    &:focus {
      color: #ffffff;
      background: $color-accent;
    }
  }
}
.secondSection_Wrapper {
  @include media-query(tablet-only) {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-bottom: 48px;
  }
}

.tabSumWrapper {
  display: flex;
}
